.social {
  @include reset-list;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  @media (min-width: $width-tablet) {
    padding-right: 15px;
  }

  @media (min-width: $width-desktop) {
    padding-right: 0;
    padding-left: 10px;
  }
}

.social__item:not(:last-child) {
  margin-right: 8px;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border: 3px solid $dark-blue-1;
  border-radius: 50%;
  line-height: 0;

  &:hover {
    border-color: $dark-blue;
  }

  &:focus {
    outline: none;
    border-color: $green;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    border-color: $dark-blue-1;
  }

  &:focus-visible {
    outline: none;
    border-color: $green;
  }

  &:active {
    border-color: $dark-blue-1;
  }

  &:active .social__icon {
    fill: $magenta-3;
  }
}

.social__icon {
  fill: $magenta;
}
