  line-height: 30px;
}

.button {
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: 16px 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0.05em;
  color: #ffffff;
  border: 3px solid #ffffff;
  border-radius: 30px;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 1200px) {
